//localStorage
export function saveToLocal(id,key,value){
  let seller = window.localStorage.seller
  if(!seller){//seller,seller
    seller = {}
    seller[id] = {}
  }else{//,seller
    //json
    seller = JSON.parse(seller)
    if(!seller[id]){ //id,seller[id]
      seller[id] = {}
    }
  }
  //seller
  seller[id][key] = value
  //
  localStorage.seller = JSON.stringify(seller)
};

//localStorage
export function loadFromLocal(id,key,def){
  let seller = window.localStorage.seller
  if(!seller){//seller
    return def
  }
  //sellerJSON,id
  seller = JSON.parse(seller)[id]
  if(!seller){ //id
    return def
  }
  //idkey
  let ret = seller[key]
  //,
  return ret || def
};



