<template>
  <div class="goods">
    <div class="menu-wrapper" ref="menuWrapper">
      <ul>
        <!-- class -->
        <li v-for="item,index in goods" class="menu-item"
        :class="{'current':currentIndex===index}" @click="selectMenus(index,$event)">
          <span class="text">
            <span v-show="item.type>0" class="icon" :class="classMap[item.type]"></span>
            {{item.name}}
          </span>
        </li>
      </ul>
    </div>
    <div class="foods-wrapper" ref="foodsWrapper">
      <ul>
        <li v-for="item in goods" class="food-list food-list-hook">
          <h1 class="title">{{item.name}}</h1>
          <ul>
            <li @click="selectFood(food,$event)"  v-for="food in item.foods" class="food-item ">
              <div class="icon">
                <img width="57" height="57" :src="food.icon">
              </div>
              <div class="content">
                <h2 class="name">{{food.name}}</h2>
                <p class="desc">{{food.description}}</p>
                <div class="extra">
                  <span class="count">{{food.sellCount}}</span>
                  <span>{{food.rating}}%</span>
                </div>
                <div class="price">
                  <span class="now">{{food.price}}</span>
                  <span class="old" v-show="food.oldPrice">{{food.oldPrice}}</span>
                </div>
                <div class="cartcontrol-wrapper">
                  <cartControl :food="food"></cartControl>
                </div>
              </div>

            </li>
          </ul>
        </li>
      </ul>
    </div>
    <cart :selectFoods="selectFoods" :delivery-price="seller.deliveryPrice" :min-price="seller.minPrice"></cart>
    <food :food="selectedFood" ref="food"></food>
  </div>
</template>

<script>
  import BScroll from 'better-scroll'
  import Cart from '../cart/cart'
  import Food from '../food/food'
  import CartControl from '../cart-control/cart-control'
  const ERROR_NO = 0
  export default {
    props:{
     seller:{
         type:Object
     }
    },
    data(){
        return{
          goods:[],
          classMap:['decrease','discount','special','invoice','guarantee'],
          listHeight:[],
          scrollY:0,
          selectedFood:{}
        }
    },
    components:{
      Cart,
      CartControl,
      Food
    },
    created(){
        this.$ajax('/goods').then((res)=>{
            if(res.data.errno === ERROR_NO){
                this.goods = res.data.data
                //dom,nextTick,dom
                //$nextTick  DOM 
                //  $nextTick DOM
                this.$nextTick(()=>{
                  this._initScroll()
                  this.calculateHeight()
                })
            }
            console.log(this.goods)
        })
    },
    computed:{
      currentIndex(){
          for(let i=0;i<this.listHeight.length;i++){
            let height1 = this.listHeight[i]
            let height2 = this.listHeight[i+1]
            //console.log(height1+','+height2)
            if(!height2 || (this.scrollY>=height1&&this.scrollY<height2)){
              return i
            }
          }
        return 0
      },
      //
      selectFoods(){
        let foods = []
        this.goods.forEach((good)=>{
          good.foods.forEach((food)=>{
            if(food.count){
              foods.push(food)
            }
          })
        })
        return foods
      },

    },
    methods:{
      //
      selectMenus(index,event){
        //better_scrollevent
        //return
        if(!event._constructed){
            return
        }
        let foodList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook')
        //dom
        let el = foodList[index]
        //better_scroll
        this.foodsScroll.scrollToElement(el,300)

      },
        _initScroll(){ //
            this.menuScroll = new BScroll(this.$refs.menuWrapper,{
                'click':true //,
            })

            this.foodsScroll = new BScroll(this.$refs.foodsWrapper,{
              probeType: 3, //
              'click':true
            })
            //,
            this.foodsScroll.on('scroll',(pos)=>{
                //
                this.scrollY = Math.abs(Math.round(pos.y))
                //console.log(this.scrollY)
            })
        },
        //
        calculateHeight(){
          //classli
          let foodList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook')
          //console.log(':'+foodList.length)
          let height = 0
          this.listHeight.push(height)
          //li,li()
          for(let i=0;i<foodList.length;i++){
              let item = foodList[i]
              height += item.clientHeight
              this.listHeight.push(height)
          }
          //console.log(this.listHeight)
        },
      //
      selectFood(food,event){
        console.log(food)
        if(!event._constructed){
          return
        }
        this.selectedFood = food
        //
        this.$refs.food.show()
      }

    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/mixin.styl"
  .goods
    position:absolute
    top: 174px
    width:100%
    bottom:46px
    display:flex
    overflow:hidden
    .menu-wrapper
      flex:0 0 80px //80px
      width:80px
      background:#f3f5f7
      .menu-item
        display:table //
        height: 54px
        width: 56px
        padding:0 12px
        line-height 14px
        font-size: 14px
        &.current
          position:relative
          background:#fff
          font-weight:700
          z-index:10
        .icon
          display:inline-block
          vertical-align:top
          width: 12px
          height: 12px
          margin-right: 2px
          background-size:12px 12px
          background-repeat:no-repeat
          &.decrease
            bg-image('../../common/image/decrease_3')
          &.discount
            bg-image('../../common/image/discount_3')
          &.guarantee
            bg-image('../../common/image/guarantee_3')
          &.invoice
            bg-image('../../common/image/invoice_3')
          &.special
            bg-image('../../common/image/special_3')
        .text
          display:table-cell
          width: 56px
          vertical-align:middle
          border-bottom-1px(rgba(7,17,27,0.1))
          font-size:12px
    .foods-wrapper
      flex:1
      .title
        padding-left: 14px
        height: 26px
        line-height: 26px
        border-left:2px solid #d9dde1
        font-size: 12px
        color:rgb(147,153,159)
        background: #f3f5f7
      .food-item
        display:flex
        margin: 18px
        padding-bottom:18px
        border-bottom-1px(rgba(7,17,27,0.1))
        &:last-child
          margin-bottom:0
        .icon
          flex:0 0 57px
          margin-right: 10px
        .content
          position:relative
          flex:1
          .name
            margin:2px 0 8px 0
            height:14px
            line-height: 14px
            font-size: 14px
            color:rgb(7,17,27)
          .desc,.extra
            line-height: 10px
            font-size: 10px
            color:rgb(147,153,159)
          .desc
            line-height:12px
            margin-bottom: 8px
          .extra
            line-height: 10px
            .count
              margin-right: 12px
          .price
            font-weight:700
            line-height: 24px
            .now
              margin-right: 8px
              font-size: 14px
              color:rgb(240,20,20)
            .old
              text-decoration :line-through
              font-size:10px
              color:rgb(147,153,159)
          .cartcontrol-wrapper
            position:absolute
            right:0
            bottom:0
</style>



